// Wrapper
.wrapper {
  width: 60vw;
  margin: auto;
  padding: 30px;
  background-color: #e4e4e4;
  border-radius: 10px;
}

// Title
.title {
  font-family: Arial, Helvetica, sans-serif;
  color: #000;
  margin: 0 0 30px;
}

// Task
.task {
  display: flex;
  justify-content: space-between;
  margin: 0 0 30px;
  font-family: Arial, Helvetica, sans-serif;
  color: #000;
  background-color: #cacaca;
  padding: 10px;
  border-radius: 5px;

  p {
    margin: 0;
    align-self: center;
  }

  &_done {
    background-color: #ffffff;
    color: #f96a6a;
  }
}
.btn-set {
  display: flex;
  gap: 15px;
}

// Button
.btn {
  outline: none;
  border: none;
  background-color: #787878;
  padding: 10px;
  border-radius: 5px;
  
  &:hover {
    background-color: #000;
    color: #fff;
    cursor: pointer;
  }

  &_red {
    background-color: #db4c4c;
    color: #fff;
  }

  &_grey {
    background-color: #9f9f9f;
  }

  &_green {
    background-color: #006722;
    color: #fff;
  }

  &_black {
    background-color: #000;
    color: #fff;
    padding: 0 30px;

    &:hover {
      background-color: #006722;
    }
  }
}

// New task input
.new-task {
  background-color: #9a9a9a;
  padding: 10px;
  display: flex;
  gap: 20px;
  border-radius: 5px;
}

.task-input {
  width: 100%;
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 10px;
}